<template>
  <div>
    <!---->
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>其他管理
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon> 表单管理
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
      <span class="screenContent">
        <img
          style="width: 10.5px;margin-right: 8px"
          src="../assets/screen.png"
        >
        <a
          href="javascript:void(0)"
          @click="isShowScreen"
        >
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="62"
      >
        <Row>
          <Col
            :xs="{ span: 5}"
            :lg="{ span: 22}"
          >
          <FormItem label="表单名称">
            <Input
              v-model="searchData.formName"
              placeholder="请填写表单名称"
            ></Input>
          </FormItem>
          </Col>
        </Row>
        <Row style="margin-top:15px">
          <Col
            :xs="{ span: 10}"
            :lg="{ span: 10}"
          >
          <FormItem label="表单类型">
            <Select
              v-model="searchData.formType"
              :transfer="transferValue"
              filterable
              clearable
            >
              <Option
                v-for="(item,key) in typeList"
                :key="key"
                :value="parseInt(key)"
              >{{ item.name }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            :xs="{ span:10 }"
            :lg="{ span:10, offset: 2 }"
          >
          <FormItem label="资金类别">
            <Select
              v-model="searchData.capitalId"
              :transfer="transferValue"
              filterable
              clearable
            >
              <Option
                v-for="item in capitalList"
                :key="item.capitalId"
                :value="item.capitalId"
              >{{ item.capitalName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            style="float:right"
            :xs="{ span: 1, offset: 1 }"
            :lg="{ span: 1, offset: 1 }"
          >
          <Button type="primary" @click="getFormList">
            确认
          </Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="createForm"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="modifyData"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="deleteFormData"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(14)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/form/exportFormListExcel')">
        导出
      </Button>
      <Table
        ref="checkTable"
        highlight-row
        class="table"
        :columns="columns"
        :data="formList"
        @on-current-change="selectRow"
      ></Table>
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!--详情弹出框-->
      <Modal
        v-model="popUp"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled">请添加表单信息</span>
          <span v-else>表单信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="form"
              :model="form"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Row>
                <Col span="12">
                <FormItem
                  label="表单类型"
                  prop="formType"
                >
                  <Select
                    v-model="form.formType"
                    :disabled="isDisabled"
                    :transfer="transferValue"
                    filterable
                    clearable
                    placeholder="请选择表单类型"
                  >
                    <Option
                      v-for="(item,key) in typeList"
                      :key="key"
                      :value="parseInt(key)"
                    >{{ item.name }}</Option>
                  </Select>
                </FormItem>
                </Col>
                <Col
                  span="12"
                >
                <FormItem
                  label="资金类别"
                  prop="capitalId"
                >
                  <Select
                    v-model="form.capitalId"
                    :disabled="isDisabled"
                    :transfer="transferValue"
                    clearable
                    filterable
                    placeholder="请选择资金类别"
                  >
                    <Option
                      v-for="item in capitalList"
                      :key="item.capitalId"
                      :value="item.capitalId"
                    >{{ item.capitalName }}</Option>
                  </Select>
                </FormItem>
                </Col>
              </Row>
              <Col span="12">
              <FormItem
                label="表单名称"
                prop="formName"
              >
                <Input
                  v-model="form.formName"
                  :disabled="isDisabled"
                  placeholder="请填写表单名称"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="表单来源"
                prop="formSource"
              >
                <Input
                  v-model="form.formSource"
                  :disabled="isDisabled"
                  placeholder="请填写表单来源"
                ></Input>
              </FormItem>
              </Col>
              <Col span="24">
              <FormItem label="使用说明">
                <Input
                  v-model="form.formRemark"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写使用说明"
                ></Input>
              </FormItem>
              </Col>
            </Form>
          </TabPane>
          <TabPane
            label="附件"
            name="name7"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="form" :is-pdf="false" :num="2" />
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">
            打印
          </Button>
          <Button icon="md-exit">
            导出
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="popUp= false"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="submitType('form')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!--/详情弹出框-->
    </Content>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
import excelExcel from '@/mixins/exportExcel.js'
export default {
  name: 'BudgetManage',
  components: {
    uploadFile
  },
  mixins: [excelExcel],
  data() {
    // 自定义序号
    const indexMethod = (row) => {
      const index = row._index
      return this.searchData.pageSize * (this.searchData.pageNo - 1) + index + 1;
    }
    return {
      isSelected: false,
      isDisabled: false,
      transferValue: true,
      // 资金类别的数据
      capitalList: [],
      formList: [],
      form: {
        formName: '',
        formType: null,
        capitalId: null,
        formSource: '',
        formRemark: ''
      },
      ruleValidateType: {
        formName: [
          { required: true, message: '请填写表单名称', trigger: 'change' }
        ],
        formType: [
          { type: 'number', required: true, message: '请填写表单类型', trigger: 'change' }
        ],
        capitalId: [
          { type: 'number', required: true, message: '请选择资金类别', trigger: 'change' }
        ],
        formSource: [
          { required: true, message: '请选择表单来源', trigger: 'change' }
        ]
      },
      total: 0,
      // 筛选内容
      searchData: {
        formName: '',
        capitalId: null,
        formType: null,
        ascending: false,
        orderBy: 'form_id',
        pageNo: 1,
        pageSize: 10
      },
      isScreen: false,
      curTime: '',
      popUp: false,
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          indexMethod: indexMethod
        },
        {
          title: '表单类型',
          key: 'formType',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row)
                }
              }
            }, params.row.formTypeName);
          }
        },
        {
          title: '资金类别',
          key: 'capitalName'
        },
        {
          title: '表单名称',
          key: 'formName'
        },
        {
          title: '表单来源',
          key: 'formSource'
        }
      ],
      typeList: {
        0: {
          name: '工程合同模板'
        },
        1: {
          name: '申报类模板'
        },
        2: {
          name: '检查类模板'
        },
        3: {
          name: '验收类模板'
        },
        4: {
          name: '前期类模板'
        },
        5: {
          name: '实施类模板'
        }
      }
    }
  },
  mounted() {
    const time = new Date().toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
    // 获取数据
    this.getCapitalList()
    this.getFormList();
  },
  methods: {
    // 分页
    pageNoChange(pageNo) {
      this.searchData.pageNo = pageNo
      this.getFormList()
    },
    pageSizeChange(pageSize) {
      this.searchData.pageSize = pageSize
      this.getFormList()
    },
    // 删除
    deleteFormData() {
      const that = this;
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要删除的内容');
        return
      }
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/form/deleteForm/' + that.currentRow.formId, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.isSelected = false;
              that.$Message.success(res.msg);
              that.getFormList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            this.$Message.error('网络连接错误');
            this.$Spin.hide();
          }, that)
        }
      });
    },
    // 搜索表单列表
    getFormList() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/form/searchForm/list', that.searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          const typeList = that.typeList
          for (const i of data.formList) {
            i.formTypeName = typeList[i.formType] ? typeList[i.formType].name : ''
          }
          that.formList = data.formList
          that.total = data.totalCount
          that.clearFormData()
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
      that.currentRow = {}
    },
    // 获取资金类别的数据
    getCapitalList() {
      const that = this;
      that.$Spin.show();
      that.commonUtil.ajaxPostUtil('/capital/searchCapital/list', { capitalName: '' }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const listData = res.data
          that.capitalList = listData.capitalList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 打开详情弹出来
    showInfo(row) {
      const data = Object.assign({}, row)
      this.clearFormData();
      this.isDisabled = true;
      this.showData(data)
    },
    showData(data) {
      this.form = data
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
      })
      this.popUp = true
    },
    // 选中一行进行修改数据
    selectRow(currentRow) {
      this.currentRow = currentRow
      this.isSelected = true;
    },
    // 修改数据
    modifyData() {
      if (!this.isSelected) {
        this.$Message.warning('您未选择需要修改的内容');
        return
      }
      this.isDisabled = false;
      this.isCreate = false;
      this.clearFormData();
      const data = Object.assign({}, this.currentRow)
      this.showData(data)
    },
    clearFormData() {
      this.form = {
        formName: '',
        formType: null,
        capitalId: null,
        formSource: '',
        formRemark: ''
      }
      this.$refs['form'].resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    createForm() {
      this.clearFormData();
      this.isDisabled = false;
      this.isCreate = true;
      this.popUp = true;
    },
    submitType(name) {
      const that = this;
      const form = that.form
      form.fileIds = that.$refs.uploadFile.getFileIds()
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          // 修改
          if (that.isCreate) {
            that.commonUtil.ajaxPostUtil('/form/addForm', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.popUp = false;
                that.$Message.success(res.msg);
                that.getFormList()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          } else {
            that.commonUtil.ajaxPutUtil('/form/updateForm', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.popUp = false;
                that.$Message.success(res.msg);
                that.getFormList()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
          that.currentRow = {}
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    // 筛选按钮
    isShowScreen() {
      this.isScreen = !this.isScreen;
    }
  }
}
</script>



